body {
    background: #e8e8e8;
    font-family: "Open Sans", Arial, sans-serif;
    color: #464646;
    padding: 0;
    margin: 0;
}

h1 {
    width: 100%;
    color: #464646;
    font-size: 34px;
    letter-spacing: 1px;
    line-height: 80px;
    height: 80px;
    margin: auto;
    text-align: center;
    border-bottom: 1px solid #464646;
}

#container {
    display: flex;
    margin-top: 80px;
    justify-content: center;
}

#info-container {
    font-size: 20px;
    margin-right: 50px;
}

#info-container h3 {
    color: #464646;
    font-size: 26px;
    margin: 0;
    padding: 0;
    padding-bottom: 10px;
    border-bottom: 1px solid #666666;
    height: 40px;
}

#player-info .player-info-container {
    font-weight: bold;
    letter-spacing: 1px;
    border-bottom: 1px solid #666666;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#player-info .player-info-container div {
    margin: 0 10px;
}

#player-info .player-color {
    height: 28px;
    width: 28px;
}

#time-taken {
    padding-top: 100px;
}

#clock {
    font-size: 54px;
    text-align: center;
    padding-top: 30px;
}

#game-over {
    font-size: 50px;
    text-align: center;
    padding-top: 50px;
}

#canvas {
    border: 1px solid #464646;
    height: 700px;
    width: 700px;
}
